#intro {
  height: 100vh; /* Full viewport height */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  padding: 20px;
}

.photo {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
}

.Introcontent {
  text-align: center;
  font-size: 3rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hello {
  font-size: 1.75rem;
  font-weight: 100;
}

.name {
  color: rgb(133, 133, 227); /* Color for the name */
}

.intropara {
  font-size: medium;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 1.5;
}
